@mixin title-desktop {
  font-weight: 600;
  font-size: 30px;
  line-height: 30px;
}

@mixin title-tablet {
  font-weight: 600;
  font-size: 24px;
  line-height: 41px;
}

@mixin title-tablet-modified {
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
}

@mixin list-style ($margin, $padding, $list-style-type) {
  margin: $margin;
  padding: $padding;
  list-style-type: $list-style-type;
}
